.r-container{
    position: relative;
    overflow: hidden;
    
    
}
.r-head{
    margin-bottom: 2rem;
    width: fit-content;
}
.r-card{
    gap: 0.6rem;
    padding: 1rem;
    border-radius: 10px;
    
    max-width: max-content;
    margin: auto;
    transition: all 300ms ease-in;
}
.r-card img{
    width: 100%;
    max-width: 15rem;
}
.r-card>:nth-child(2){
    font-size: 1.2rem;
    font-weight: 600;
}
.r-card>:nth-child(3){
    font-size: 1.5rem;
}
.r-card>:nth-child(4){
    font-size: 0.7rem;
    width: 15rem;
}
.r-card:hover{
    scale: 1.025;
    cursor: pointer;
    background: linear-gradient(
        180deg, 
        rgba(255,255,255,0) 0%,
        rgba(136,160,255,0.46) 217%
    );
    box-shadow: 0px 72px 49px -51px rgba(136, 160, 255, 0.21);
}
/* the swiper css is not allowing us to change the shadow -it has its own settings , 
here is how we disable it */

.swiper-horizontal{
    overflow: visible;
}
.r-buttons{
    
    gap: 1rem;
    padding-top: 1rem;
    margin-bottom: 2rem;
    
    
    
}
.r-buttons button{
    font-size: 1.2rem;
    padding: 0.2rem 0.8rem;
    color: blue;
    border: none;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
}
.r-buttons>:nth-child(1){
    background-color: #EEEEFF;
}
.r-buttons>:nth-child(2){
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.25);
}
@media(max-width: 640px){
    .r-head{
        align-items: center;
        
    }
}